@import url(https://fonts.googleapis.com/css2?family=Oxanium:wght@400;700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#wrapper{font-weight:bold;font-size:1.5em;color:#45939E;font-family:Oxanium,sans-serif}#wrapper #preview,#wrapper #editor{font-family:Roboto, sans-serif;font-size:0.6em;font-weight:400}#wrapper #preview{min-height:60vh;width:45vw;border:2px solid #83C2BE;border-radius:15px;padding-left:5px;text-align:left;color:#1C1A1B;background-color:#F2F9F9;display:block;position:relative;z-index:1}#wrapper #preview::after{content:"";opacity:0.3;background-image:url(/markdown-previewer/static/media/hi.86fb662b.webp);background-repeat:no-repeat;background-size:250px;background-position:bottom right;top:0;left:0;bottom:0px;right:0;position:absolute;z-index:-1}#wrapper #editor{font-size:0.6em;min-width:45vw;min-height:80vh;color:#45939E;background-color:#F2F9F9}#wrapper #preview blockquote{border-left:3px solid #45939E;color:#224b4b;padding-left:5px;margin-left:25px}#wrapper #preview code{background:white;color:#45939E;padding:1px 4px 2px 4px;font-weight:bold;display:inline-block}#wrapper #preview a{color:#83C2BE}#wrapper #preview li::marker{color:#515459}#wrapper #preview h1{color:#45939E}#wrapper #preview h2,#wrapper #preview h3,#wrapper #preview h4,#wrapper #preview h5,#wrapper #preview h6{color:#1C1A1B}#wrapper #preview td,#wrapper #preview th{border:2px solid #83C2BE;padding-left:5px;padding-right:5px}

